import React from "react";
import { getStuff } from "./api.js";

function App() {
  const [val, set] = React.useState("none");
  React.useEffect(() => {
    getStuff().then((retval) => {
      console.log(retval.data);
      set(retval.data);
    });
  }, []);
  return (
    <div className="App">
      <header className="App-header">
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React {val}
        </a>
      </header>
    </div>
  );
}

export default App;
